export default {
		data() {
			return {
				
				typeIndex:0,
				xiadan:1,//
				currentDay:0,//
				showIndex:0,//
				dayInfo:[],//
				dayID:[],
				show: false,
				currentIndex: 0,
				type:['',''],
				zhifu:false,
        dayChange: false, //
				value:'',
				list: [
					'', '', 'title3', 'title4',
				],
				id: '',//id
				goods_list:[],//
				detail:{},//
				// 
				          radiolist1: [
							  {
				            name: '',
				            disabled: false
				          },
				            {
				              name: '',
				              disabled: false
				            },
				            
				          ],
				          // u-radio-groupv-modelradioname
				          radiovalue1: '',
			};
		},
		onLoad(options) {
			//  
			this.currentIndex = options.id
			let xx= Number(options.id)+1
			console.log(xx)
			let goods_list={
			  	'tid':xx.toString()
			}
			this.$fn.request('goods/goods_list', 'GET',goods_list).then(res => {
				console.log(res.data.data.data,'')
				this.goods_list=res.data.data.data
			})
			
			// 
			let type={}
			this.$fn.request('/pay/list', 'GET',type).then(res => {
				console.log(res.data.data,'')
				
			})
			
			
			// ------------
			let data = {

			}
			// 
			this.$fn.request('goods/goods_type', 'GET',data).then(res => {
				// console.log(res.data.data,'')
				this.list=res.data.data
			})
			
			
			
	
		},
		onShow() {
			uni.pageScrollTo({
				duration:100,
				scrollTop:120,
			})
			this.$store.state.one=true
			this.$store.state.two=true
			this.$store.state.three=true
			this.$store.state.four=true
		},
		methods: {
			jia(){
				this.xiadan++
			},
			jian(){
				if(this.xiadan==0){
					return
				}
				this.xiadan--
			},
			typeClick(index){//
				this.typeIndex=index
				console.log(index)
			},
			danxuan(e){
				console.log(e)
			},
			rightClick(index,item){//
			
				this.currentDay=index
        this.dayChangeClose()
				console.log(item.id)
			},
			shang(){//
			console.log(this.showIndex)
			 //    if(this.showIndex==0){
				// 	return
				// }
				this.showIndex--
			},
			next(){//
				this.showIndex++
				// if(this.showIndex>3){
				// 	return
				// }
				
				if(this.showIndex==1){
					//
					let goods_day={
						'gid':this.detail.id
					}
					this.$fn.request('goods/goods_day', 'GET',goods_day).then(res => {
						// console.log(res,'')
						this.dayID=res.data.data.data
						console.log(this.dayID,'')
					})
				}
				
				
				
			},
			pay(){ //
			console.log(this.xiadan.toString())
				let data={
					'gid':this.detail.id.toString(),
					'num':this.xiadan.toString(),
					'did':this.dayID[this.currentDay].id.toString(),
					'pid':(this.typeIndex+1).toString(),
					'cid':'0'
				}
				this.$fn.request('/pay/order', 'POST',data).then(res => {
					console.log(res.data,)
					// this.goods_list=res.data.data.data
					if(res.data.code == 1){
						uni.showToast({
							title:'',
							duration:1000,
							icon:"success"
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							duration:1000,
							icon:"error"
						})
					}
				})
			},
			// -----------
			changeDay(e){
				// this.currentDay=e
			},
			changeNumber(e){//
				this.xiadan=e
				console.log(this.xiadan)
			},
			
			lijirengou(item){//
				this.zhifu=true
				this.showIndex=0
				
				// 
				let goods_day={
					'gid':this.detail.id
				}
				this.$fn.request('goods/goods_day', 'GET',goods_day).then(res => {
					// console.log(res,'')
					this.dayID=res.data.data.data
					console.log(this.dayID,'')
				})
			},
			bian(index,id) {//
				this.currentIndex = index
				// 
				console.log(id)
				let goods_list={
				  	'tid':id
				}
				this.$fn.request('goods/goods_list', 'GET',goods_list).then(res => {
					console.log(res.data.data.data,'')
					this.goods_list=res.data.data.data
				})
			},
			go(index) {
				this.show=true
				this.id=index
				// 
				let goods_one={
					"id":this.id
				}
				this.$fn.request('goods/goods_one', 'GET',goods_one).then(res => {
					console.log(res.data.data,'')
					this.detail=res.data.data
				}) 
				uni.pageScrollTo({
					duration:100,
					scrollTop:0,
				})
			},
			open() {
				// console.log('open');
			},
			close() {
				this.show = false
				uni.pageScrollTo({
					duration:100,
					scrollTop:120,
				})
				// console.log('close');
			},
			zhifuopen(){
				
			},
			zhifuclose(){
				this.zhifu=false
			},
      dayChangeOpen(){
      	
      },
      dayChangeClose(){
      	this.dayChange=false
      },
			
			 groupChange(n) {
			        console.log('groupChange', n);
			      },
			      radioChange(n) {
			        console.log('radioChange', n);
			      }
		}
	}